.btn {
    border: 1px solid black;
    background-color: white;
    border-radius: 2rem;
    padding: 0.5rem 1rem;
    margin: 1rem;
    color: black;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
}

.btn:hover {
    color: white;
    background-color: black;
}


